.contactTop {
  background: linear-gradient(
    135deg,
    rgba(156, 39, 176, 0.72) 0%,
    rgba(103, 58, 183, 0.8) 100%
  );
  padding: 30px;
  text-align: center;
  font-family: "Roboto", sans-serif;
  color: rgb(240, 248, 255);
}
h4.clfxtitle {
  font-size: 17px;
  margin-top: auto;
}
h4.clfxtitle a {
  color: rgb(156, 39, 176);
}

.section-title {
  font-weight: 400;
  font-size: 28px;
  color: black !important;
  letter-spacing: 1.75px;
  margin-bottom: 0;
  margin-top: 20px;
  margin-bottom: 30px;
  text-align: center;
}
.list_item {
  display: flex;
  justify-content: center;
}

.clearfix {
  zoom: 1;
}
.list_item .list_left {
  color: rgb(103, 58, 183);
}

.list_item .list_left {
  width: 80px;
  height: 80px;
  line-height: 80px;
  font-size: 50px;
  overflow: hidden;
  text-align: center;
  float: left;
}
.list_item .list_right {
  padding: 5px 0 0;
  word-wrap: break-word;
}
p.contactp {
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}
