.faq__main_top {
  padding-top: 50px;
  padding-bottom: 50px;
  display: list-item;
  background: #fff
}
.faq__main {
  display: block;
  /* position: relative; */
  box-sizing: border-box;
  padding: 30px;
  width: 100%;
  max-width: 920px;
  background-color: #fff;
  margin: 0 auto;
  box-shadow: 0px 0px 30px 0px #ddd;
  border-radius: 15px;
}


.faq__main h1{
  color: #555;
}

.topic {
  padding: 0;
  padding-bottom: 1px;
}
.open {
  cursor: pointer;
  display: block;
  padding: 0px;
}
.open:hover {
  opacity: 0.7;
}
.expanded {
  /* background-color: rgba(156, 39, 176, 0.24); */
  transition: all 0.3s ease-in-out;
}
.question {
  padding-right: 35px;
  padding-bottom: 0px;
  font-size: 19px;
  color: rgb(156, 39, 176);
  font-family: "Roboto", sans-serif;
  line-height: 1.8;
  margin: 5px !important;
  font-weight: 600
}
.answer {
  font-size: 16px;
  line-height: 30px;
  display: none;
  margin-bottom: 35px;
  color: rgb(0, 0, 0);
  text-align: justify;
  padding-left: 20px;
  padding-right: 20px;
  font-family: "Roboto", sans-serif;
}
.faq-t {
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  display: inline-block;
  float: right;
  position: relative;
  top: -55px;
  right: 10px;
  width: 10px;
  height: 10px;
  background: transparent;
  border-left: 2px solid #ccc;
  border-bottom: 2px solid #ccc;
  transition: all 0.3s ease-in-out;
}
.faq-o {
  top: -50px;
  -moz-transform: rotate(-224deg);
  -ms-transform: rotate(-224deg);
  -webkit-transform: rotate(-224deg);
  transform: rotate(-224deg);
}
@media only screen and (max-width: 480px) {
  .faq-t {
    display: none;
  }
  .question {
    padding-right: 0px;
  }
  main {
    padding: 10px;
  }
  .answer {
    margin-bottom: 30px;
    padding-left: 0px;
    padding-right: 0px;
  }
}
