.top___index {
  background: #fff
}

.about-section {
  position: relative;
  color: rgb(0, 0, 0) !important;
  font-family: "Roboto", sans-serif;
}

.about-section .title {
  color: #683e90 !important; 
}
.about-section .text {
  color: #333 !important; 
}

.about-section .content-column {
  position: relative;
  margin-bottom: 40px;
}

.about-section .content-column .inner-column {
  position: relative;
  padding-top: 50px;
  padding-right: 100px;
}

.about-section .content-column .text {
  position: relative;
  color: #777777;
  font-size: 15px;
  line-height: 2em;
  margin-bottom: 40px;
}

.about-section .content-column .email {
  position: relative;
  color: #252525;
  font-weight: 700;
  margin-bottom: 50px;
}

.about-section .image-column {
  position: relative;
  margin-bottom: 50px;
}

.about-section .image-column .inner-column {
  position: relative;
  padding: 40px 40px 0px 0px;
  margin-left: 50px;
}

.about-section .image-column .inner-column:after {
  position: absolute;
  content: "";
  right: 0px;
  top: 0px;
  left: 40px;
  bottom: 100px;
  z-index: -1;
  border: 2px solid #d7a449;
}

.about-section .image-column .inner-column .image {
  position: relative;
}

.about-section .image-column .inner-column .image:before {
  position: absolute;
  content: "";
  left: -50px;
  bottom: -50px;
  width: 299px;
  height: 299px;
  /* background: url(img/pattern-2.png) no-repeat; */
}
.sssssssss {
  text-align: center;
}
.about-section .image-column .inner-column .image img {
  position: relative;
  width: 100%;
  display: block;
}

.about-section .image-column .inner-column .image .overlay-box {
  position: absolute;
  left: 40px;
  bottom: 48px;
}

.about-section .image-column .inner-column .image .overlay-box .year-box {
  position: relative;
  color: #252525;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.4em;
  padding-left: 125px;
}

.about-section
  .image-column
  .inner-column
  .image
  .overlay-box
  .year-box
  .number {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 110px;
  height: 110px;
  color: #d7a449;
  font-size: 68px;
  font-weight: 700;
  line-height: 105px;
  text-align: center;
  background-color: #ffffff;
  border: 1px solid #000000;
}
.about-section .btn-style-three {
  color:#683e90 !important; 
}

.about-section .btn-style-three:hover {
  color: #fff !important; ;
  background: rgb(102, 45, 145);
}

.about-section .btn-style-three {
  position: relative;
  color: rgb(240, 255, 255);
  font-size: 18px;
  font-weight: 700;
  margin-top: 20px;
  background: none;
  transition: 0.5s;
  display: inline-block;
  padding: 10px 20px;
  text-transform: capitalize;
  border: 2px solid rgb(102, 45, 145);
  font-family: "Roboto", sans-serif;
  border-radius: 35px;
}

.sec-title2 {
  color: #fff;
}
.sec-title {
  position: relative;
  padding-bottom: 40px;
}
.sec-title .title {
  position: relative;
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  padding-right: 50px;
  margin-bottom: 15px;
  display: inline-block;
  text-transform: capitalize;
}
.sec-title .title:before {
  position: absolute;
  content: "";
  right: 0px;
  bottom: 7px;
  width: 40px;
  height: 1px;
  background-color: #bbbbbb;
}

.about-us {
  padding-top: 20px;
  padding-bottom: 30px;
}

.about-us::after {
  clear: both;
  content: "";
  display: table;
}
@media only screen and (min-width: 1000px) {
  .about-us article,
  .about-us aside {
    float: left;
    width: 47.5%;
  }
}
@media only screen and (min-width: 1000px) {
  .about-us article {
    margin-right: 5%;
  }
}
.about-us article h2 {
  margin: 0;
  margin-bottom: 30px;
  font-size: 40px;
  font-weight: 300;
  color: #683e90;
}

.about-us article p {
  color: #333 !important;
}
.text {
  line-height: 1.7;
  color: rgb(240, 255, 255);
}
.about-us article p {
  margin-bottom: 2em;
  font-size: 16px;
  line-height: 28px;
  font-weight: 300;
  color: #ffffff;
}
.about-us aside ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.about-us aside ul::after {
  clear: both;
  content: "";
  display: table;
}
.about-us aside ul li {
  display: table;
  width: 100%;
  height: 170px;
}
@media only screen and (min-width: 1000px) {
  .about-us aside ul li {
    float: left;
    width: 33.3333333333%;
  }
}
.about-us aside ul li:first-child {
  background-color: #afce44;
}
.about-us aside ul li:nth-child(2) {
  background-color: #35b8e4;
}
.about-us aside ul li:nth-child(3) {
  background-color: #308fc3;
}
.about-us aside ul li h3 {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.about-us aside ul li h3 span {
  display: block;
  color: #fff;
}
.about-us aside ul li h3 span:first-child {
  font-family: "Roboto", sans-serif;
  font-size: 60px;
  font-weight: 700;
}
.about-us aside ul li h3 span:nth-child(2) {
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}

.sectionClass {
  padding: 20px 0px 50px 0px;
  position: relative;
  display: block;
}

.fullWidth {
  width: 100% !important;
  display: table;
  float: none;
  padding: 0;
  min-height: 1px;
  height: 100%;
  position: relative;
}

.sectiontitle {
  background-position: center;
  margin: 30px 0 0px;
  text-align: center;
  min-height: 20px;
}

.sectiontitle h2 {
  font-size: 30px;
  color: #222;
  margin-bottom: 0px;
  padding-right: 10px;
  padding-left: 10px;
}

.headerLine {
  width: 160px;
  height: 2px;
  display: inline-block;
  background: #101f2e;
}

.projectFactsWrap {
  display: flex;
  margin-top: 30px;
  flex-direction: row;
  flex-wrap: wrap;
}

#projectFacts .fullWidth {
  padding: 0;
}

.projectFactsWrap .item {
  width: 25%;
  height: 100%;
  padding: 50px 0px;
  text-align: center;
}

.projectFactsWrap .item:nth-child(1) {
  background: rgb(16, 31, 46);
}

.projectFactsWrap .item:nth-child(2) {
  background: rgb(18, 34, 51);
}

.projectFactsWrap .item:nth-child(3) {
  background: rgb(21, 38, 56);
}

.projectFactsWrap .item:nth-child(4) {
  background: rgb(23, 44, 66);
}
.projectFactsWrap .item p.number {
  font-size: 40px;
  font-weight: bold;
  margin-top: 15px;
  font-family: "Roboto", sans-serif;
}

.projectFactsWrap .item p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 18px;
  margin: 0;
  padding: 10px;
  font-family: "Roboto", sans-serif;
}

.projectFactsWrap .item span {
  width: 60px;
  background: rgba(255, 255, 255, 0.8);
  height: 2px;
  display: block;
  margin: 0 auto;
}

.projectFactsWrap .item i {
  vertical-align: middle;
  font-size: 50px;
  color: rgba(255, 255, 255, 0.8);
}

.projectFactsWrap .item:hover i,
.projectFactsWrap .item:hover p {
  color: white;
}

.projectFactsWrap .item:hover span {
  background: white;
}

@media (max-width: 786px) {
  .projectFactsWrap .item {
    flex: 0 0 50%;
  }
}

/* AUTHOR LINK */

footer {
  z-index: 100;
  padding-top: 50px;
  padding-bottom: 50px;
  width: 100%;
  bottom: 0;
  left: 0;
}

footer p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 16px;
  opacity: 0;
  font-family: "Open Sans";
  width: 100%;
  word-wrap: break-word;
  line-height: 25px;
  -webkit-transform: translateX(-200px);
  transform: translateX(-200px);
  margin: 0;
  -webkit-transition: all 250ms ease;
  -moz-transition: all 250ms ease;
  transition: all 250ms ease;
}

footer .authorWindow a {
  color: white;
  text-decoration: none;
}

footer p strong {
  color: rgba(255, 255, 255, 0.9);
}

.about-me-img {
  width: 120px;
  height: 120px;
  left: 10px;
  /* bottom: 30px; */
  position: relative;
  border-radius: 100px;
}

.about-me-img img {
}

.authorWindow {
  width: 600px;
  background: #75439a;
  padding: 22px 20px 22px 20px;
  border-radius: 5px;
  overflow: hidden;
}

.authorWindowWrapper {
  display: none;
  left: 110px;
  top: 0;
  padding-left: 25px;
  position: absolute;
}

.trans {
  opacity: 1;
  -webkit-transform: translateX(0px);
  transform: translateX(0px);
  -webkit-transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  transition: all 500ms ease;
}

@media screen and (max-width: 768px) {
  .authorWindow {
    width: 210px;
  }

  .authorWindowWrapper {
    bottom: -170px;
    margin-bottom: 20px;
  }

  footer p {
    font-size: 14px;
  }
}
