html,
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  line-height: 20px;
}
a {
  color: #ffffff;
}

#testim, #start{
  -webkit-clip-path: polygon(0 0, 0 93%, 22% 100%, 750% 0);
  clip-path: polygon(0 0, 0 90%, 22% 100%, 750% 0)
}

nav ul a {
  padding: 33px 20px;
  color: #757575;
  font-weight: 400;
}

.text-primary {
  color: #683e90 !important;
}

nav .sidenav-trigger{
  padding: 18px 0px;
}

nav ul a.active {
  background: rgb(102, 46, 145) !important;
  color: #fff
}


footer.mdl-mega-footer {
  padding: 0 !important;
}

footer#devise-footer {
  margin-top: 35px;
  margin-bottom: 35px;
  padding: 0;
}




.text-center {
  text-align: center
}
.text-left {
  text-align: left
}
.text-right {
  text-align:right
}

