#registration-tab button{
  width: 50%
}

#registration-tab {
  margin-bottom: 30px
}
#registration-tab header{
  border-radius: 100px;
}

#registration-tab header button[aria-selected="true"], 
#registration-tab header button[aria-selected="true"]:focus{
  background: #2a0e46 !important;
  /* border-bottom: 5px solid #7d13de; */
