}

.registration_form{
  font-family: 'Roboto', sans-serif;
  background-color: #f5f5f5!important
}

.registration_form form {
  border: 1px solid #eee;
  box-shadow: 0 -3px 31px 0 rgba(0, 0, 0, 0.05), 0 6px 20px 0 rgba(0, 0, 0, 0.02);
  border-radius: 5px
}

#home{
