}

.rounded[type="submit"] {
  border-radius: 50px !important;
}

.btn-primary{
  background-color: #683e90
}

.btn-primary:hover{
  background-color: #2a0e46
}

.pt-30 {
  padding-top: 30px;
}
.mt-20 {
  margin-top: 20px;
}
.mb-20 {
