}


#feature-on{
  background: #fff;
  padding: 100px 0px;

}

#feature-on .div-title h4{
  text-transform: uppercase;
  position: relative;
  margin-bottom: 30px;
}

#feature-on .div-title h4::before,
#feature-on .div-title h4::after{
    position: absolute;
    left: 50%;
    content: "";
    z-index: 9;
    transform: translateX(-50%);
}


#feature-on .div-title h4::before{
    width: 200px;
    border-bottom: 1px solid  #683e90;
    bottom: -10.5px;
}

#feature-on .div-title h4::after{
    width: 100px;
    border-bottom: 5px solid #683e90;
    bottom: -12px;
}

#feature-on .owl-theme img{
  width: 100px
}

#feature-on .owl-theme .owl-item .item{
  display: flex;
  align-items: center;
  justify-content: center;
}




.bg-light{
  background:#eef4f7 !important; 
}



#feature-on .owl-stage{
  display: flex;
  align-items: center
}



.member-of{
}

.member-of .div-title h4{
  color: #fff !important
}
.member-of  .div-title h4::before{
    border-bottom: 1px solid  #fff !important;
}

.member-of .div-title h4::after{
    border-bottom: 5px solid #fff !important;
}

.member-of p{
  color: #fff !important
}

.bg-dark{
  background:#1e2940 !important
}
.bg-primary{
  background:#683e90 !important
}

.bg-dark h1,
.bg-dark h2,
.bg-dark h3,
.bg-dark h4,
.bg-dark h5,
.bg-dark h6{
  color: #fff !important
}


.bg-primary h1,
.bg-primary h2,
.bg-primary h3,
.bg-primary h4,
.bg-primary h5,
.bg-primary h6{
  color: #fff !important
}

/* bg-light */


#testim{
  padding-top: 30px
}
#testim.bg-primary p{
  color: #fff !important
}

svg#wave-shape{
  position: absolute;
  bottom: 0px;
  left: 0px; 
  z-index: 9;
}
svg #wave-shape {
  fill:#fff; stroke-linejoin:round; stroke:#ffffff
}


.overlay{
    width: 100%;
    height: 100%;
    background: #683e8fc9;
    position: absolute;
    bottom: 0px;
    z-index: 1;
}


.bg-light h1,
.bg-light h2,
.bg-light h3,
.bg-light h4,
.bg-light h5,
.bg-light h6,
.bg-light p{
  color: #222 !important
}



/* Team */


#our-team{
  background: #fff;
  padding: 100px 0px;

}

#our-team .div-title h4{
  text-transform: uppercase;
  position: relative;
  margin-bottom: 30px;
}

#our-team .div-title h4::before,
#our-team .div-title h4::after{
    position: absolute;
    left: 50%;
    content: "";
    z-index: 9;
    transform: translateX(-50%);
}


#our-team .div-title h4::before{
    width: 200px;
    border-bottom: 1px solid  #683e90;
    bottom: -10.5px;
}

#our-team .div-title h4::after{
    width: 100px;
    border-bottom: 5px solid #683e90;
    bottom: -12px;
}


#our-team .owl-theme .owl-item .item{
  display: flex;
  align-items: center;
  justify-content: center;
}


#our-team .team-picture{
  border-radius: 100%;
  overflow: hidden;
  padding: 10px 50px;
  -webkit-transition: 0.7s ease 0s;
  -moz-transition: 0.7s ease 0s;
  -ms-transition: 0.7s ease 0s;
  -o-transition: 0.7s ease 0s;
  transition: 0.7s ease 0s;
}
#our-team .team-picture img{
  border-radius: 100%;
  padding: 10px;
  border: 2px solid transparent;
}

#our-team .item:hover img{
  border-color: #683e90;
  -webkit-transition: 0.7s ease 0s;
  -moz-transition: 0.7s ease 0s;
  -ms-transition: 0.7s ease 0s;
  -o-transition: 0.7s ease 0s;
  transition: 0.7s ease 0s;
}

#our-team .member-title{
  text-align: center;
  margin: 0px;
  margin-top: 20px;
  text-transform: uppercase;
  font-size: 20px !important;
}

#our-team .item{
  text-align: center;
  margin-bottom: 50px;
}

#our-team .member-designation{
  margin: 0px;
  font-size: 14px;
  font-style: italic;
  font-weight: 400
}


#homepage-form{
  background: rgba(141, 3, 153, 0.39);
  padding: 30px 20px;
  border-radius: 10px;
}